.Comment-collegespace-main {
  width: 100%;
  display: block;
  background: #f6f7fb;
}
.Comment-collegespace-main .school-warp {
  width: 1200px;
  margin: 0 auto;
}
.Comment-collegespace-main .space-img {
  padding-top: 20px;
  width: 100%;
  background: #fff;
}
.Comment-collegespace-main .space-img img {
  display: block;
  margin: 0px auto;
}
.Comment-collegespace-main .pagebanner {
  padding-top: 76px;
  display: block;
  width: 100%;
  min-width: 1100px;
  text-align: center;
}
.Comment-collegespace-main .pagebanner .page-mian {
  position: relative;
  display: block;
  text-align: center;
}
.Comment-collegespace-main .pagebanner .page-mian img {
  display: block;
  width: 100%;
}
.Comment-collegespace-main .pagebanner .page-mian .text {
  background: linear-gradient(270deg, #C64848 0%, rgba(198, 72, 72, 0) 100%);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Comment-collegespace-main .pagebanner .page-mian .text .h4 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 120.7%;
  color: #FFFFFF;
  display: inline-block;
  margin-right: 10px;
  margin-bottom: 0;
}
.Comment-collegespace-main .pagebanner .page-mian .text .span {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120.7%;
  color: #FFFFFF;
  opacity: 0.7;
  display: inline-block;
}
.Comment-collegespace-main .pagebanner .page-mian .text .banner-search {
  float: right;
  width: 428px;
  height: 38px;
  background: #FFFFFF;
  border-radius: 6px;
  margin-right: 30px;
}
.Comment-collegespace-main .pagebanner .page-mian .text .banner-search .ant-input {
  width: 100%;
  display: block;
}
.Comment-collegespace-main .pagebanner .page-mian .text .banner-search .ant-input-search-icon {
  color: #1A8ECC;
}
.Comment-collegespace-main .title-top {
  display: block;
  border-bottom: 2px solid #E1E1E1;
}
.Comment-collegespace-main .title-top .h3 {
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 33px;
  color: #0D1731;
  display: inline-block;
  padding: 0 10px 15px 10px;
  position: relative;
  margin-bottom: 0;
}
.Comment-collegespace-main .title-top .h3::after {
  position: absolute;
  left: 0;
  bottom: -2px;
  background: #DE2C2C;
  content: "";
  height: 2px;
  width: 100%;
}
.Comment-collegespace-main .solution-main {
  width: 1200px;
  margin: 30px auto 0;
  display: block;
  background: #ffffff;
  padding: 27px 35px;
}
.Comment-collegespace-main .solution-main .title {
  display: block;
  text-align: center;
  margin-bottom: 50px;
}
.Comment-collegespace-main .solution-main .title span {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 27px;
  color: #0D1731;
  opacity: 0.5;
  display: block;
}
.Comment-collegespace-main .solution-main .title h3 {
  font-style: normal;
  font-weight: 500;
  font-size: 30px;
  line-height: 41px;
  color: #0D1731;
  position: relative;
  display: block;
  padding-bottom: 10px;
}
.Comment-collegespace-main .solution-main .title h3::after {
  position: absolute;
  left: 50%;
  bottom: 0;
  margin-left: -20px;
  width: 40px;
  height: 4px;
  content: "";
  background: #DE2C2C;
  border-radius: 2px;
}
.Comment-collegespace-main .solution-main .solution-bottom {
  display: block;
  width: 100%;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li {
  margin-bottom: 50px;
  display: block;
  width: 100%;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .left {
  padding-top: 35px;
  float: left;
  width: 49%;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .left .img {
  float: left;
  width: 24.4%;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .left .img img {
  width: 100%;
  display: block;
  height: 100%;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .left .text {
  float: left;
  width: 70%;
  padding-left: 5%;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .left .text span {
  font-style: normal;
  font-weight: normal;
  font-size: 13.9867px;
  line-height: 19px;
  display: block;
  align-items: center;
  color: #0D1731;
  opacity: 0.3;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .left .text h4 {
  font-style: normal;
  font-weight: normal;
  font-size: 32.2858px;
  line-height: 44px;
  display: block;
  align-items: center;
  color: #0D1731;
  margin-bottom: 0;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .left .text p {
  padding-top: 18px;
  display: block;
  font-style: normal;
  font-weight: normal;
  font-size: 19.078px;
  line-height: 26px;
  text-align: left;
  color: #0D1731;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .right {
  float: right;
  padding-right: 50px;
}
.Comment-collegespace-main .solution-main .solution-bottom .list-li .right span {
  border-radius: 11.1823px;
  display: block;
  width: 100%;
}
.Comment-collegespace-main .Partner-school {
  margin: 0 auto 30px;
  width: 1200px;
}
.Comment-collegespace-main .Partner-school .tit-top {
  padding: 30px 0 10px;
  margin: 0px auto;
  width: 150px;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 33px;
  color: #0D1731;
  text-align: center;
  position: relative;
}
.Comment-collegespace-main .Partner-school .tit-top::after {
  position: absolute;
  left: 50%;
  margin-left: -28px;
  height: 2px;
  background: #DE2C2C;
  content: "";
  width: 56px;
  bottom: 0;
  border-radius: 2px;
}
.Comment-collegespace-main .Partner-school .p-des {
  margin: 25px auto 42px;
  display: block;
  padding: 0 30px;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #000000;
  opacity: 0.4;
}
.Comment-collegespace-main .Partner-school .img-box {
  overflow: hidden;
  padding: 0 0 0px;
  margin: 10px 0 0px;
}
.Comment-collegespace-main .Partner-school .img-box img {
  margin-top: 20px;
  width: 23.584%;
  max-height: 80px;
  margin-right: 1.888%;
}
.Comment-collegespace-main .Partner-school .img-box img:nth-child(4n) {
  margin-right: 0;
}
.Comment-collegespace-main .newslist-main {
  display: block;
  margin-bottom: 43px;
}
.Comment-collegespace-main .newslist-main .list {
  margin-top: 28px;
  background: #fff;
  width: 100%;
  display: block;
  padding: 33px 35px 50px;
}
.Comment-collegespace-main .newslist-main .list .list-bottom {
  padding-top: 35px;
  display: block;
}
.Comment-collegespace-main .newslist-main .list .list-bottom .list-li {
  width: 22.7%;
  margin-right: 3.0666%;
}
.Comment-collegespace-main .newslist-main .list .list-bottom .list-li:nth-child(4n) {
  margin-right: 0;
}
.Comment-collegespace-main .newslist-main .list .list-bottom .list-li:hover h4 {
  color: #DE2C2C;
}
.Comment-collegespace-main .newslist-main .list .list-bottom .img {
  width: 100%;
  height: 150px;
  display: block;
}
.Comment-collegespace-main .newslist-main .list .list-bottom .img img {
  display: block;
  width: 100%;
  height: 100%;
}
.Comment-collegespace-main .newslist-main .list .list-bottom h4 {
  font-style: normal;
  font-weight: normal;
  font-size: 15.7031px;
  line-height: 20px;
  color: #0D1731;
  display: block;
  margin-top: 15px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 0;
  height: 60px;
}
.Comment-collegespace-main .newslist-main .list .list-bottom p {
  font-style: normal;
  font-weight: normal;
  font-size: 12.848px;
  line-height: 120.7%;
  color: #0D1731;
  opacity: 0.5;
  display: block;
  margin-top: 10px;
}
